/* For desktop: */
.root {
  display: flex;
}

.controls {
  height: 100vh;
  width: 375px;
  background-color: white;
  display: flex;
  flex-direction: column;
  box-shadow: 4px 0px 16px -2px rgba(0,0,0,0.15);
  z-index: 1;
  box-sizing: border-box;
}

.content {
  position: relative;
  height: 100vh;
  flex: 1;
  padding: 24px;
  overflow-y: auto;
  overflow-x: hidden;
  color: white;
  background: #3b4458;
  box-sizing: border-box;
}

@media only screen and (max-width: 720px) {
  /* For mobile: */
  .root {
    display: flex;
    flex-direction: column;
  }
  .controls {
    height: 100vh;
    width: auto;
    background-color: white;
    display: flex;
    flex-direction: column;
    box-shadow: 4px 0px 16px -2px rgba(0,0,0,0.15);
    z-index: 1;
    box-sizing: border-box;
  }

  .content {
    position: relative;
    height: 100vh;
    width: 100%;
    padding: 24px;
    overflow-y: auto;
    overflow-x: hidden;
    color: white;
    background: #3b4458;
    box-sizing: border-box;
  }
}

.field {
  margin-bottom: 36px;
}

.slider {
  padding-bottom: 12px;
}

.sliderLabel {
  display: flex;
  justify-content: space-between;
  padding-bottom: 12px;
}

.radioGroupLabel {
  display: flex;
  justify-content: space-between;
}

.tabs {

}

.tabRoot {
  width: 25% !important;
  min-width: 0px !important;
  margin: 0 !important;
}

.tabLabel {
  padding: 0 !important;
  margin: 0 auto !important;
}

.controlsHeader {
  position: relative;
  height: 200px;
  min-height: 200px;
  display: flex;
  flex-direction: column;
  box-shadow: 0 4px 2px -2px rgba(0,0,0,0.1);
}

.appTitle {
  composes: children-center from global;
  padding-top: 24px;
  flex: 2;
  composes: text-grey-7 from global;
  font-size: 24px;
}

.appTitle img {
  padding: 12px;
  padding-top: 0px;
  padding-bottom: 2px;
}

.appSubtitle {
  flex: 1;
  composes: text-md from global;
  composes: text-grey-6 from global;
}

.controlsBody {
  flex: 1;
  padding: 48px;
  overflow-x: hidden;
}

.controlsFooter {
  height: 48px;
  background: white;
  box-shadow: 0 -4px 2px -2px rgba(0,0,0,0.1);
}

.mapContainer {
  min-width: 200px;
  width: 90%;
  margin: 0 auto;
}
